.header {
  font-family: "Gilroy", sans-serif;

  &__top {
    padding: 20px 0;
    .container {
      display: flex;
      justify-content: space-between;
      align-items: end;
    }
  }

  &__logo {
    display: flex;
    align-items: center;
    gap: 16px;

    &-descr {
      font-family: "Gilroy", sans-serif;
      font-weight: 600;

    }
  }

  &__time {
    font-family: "Gilroy", sans-serif;
    font-weight: 700;

    &-text {
      font-weight: 400;
    }

    span {
      display: block;
      margin-bottom: 2px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__phone {
    &-link {
      font-family: "Gilroy", sans-serif;
      display: block;
      color: #4C4B47;
      font-weight: 600;
    }
  }

  &__buttons {
    &-descr {
      font-family: "Gilroy", sans-serif;
      font-size: 12px;
      text-align: center;
      margin-bottom: 6px;

      span {
        font-weight: 600;
      }
    }

    &-btn {
      font-family: "Gilroy", sans-serif;
      color: #fff;
      background: #4BBEEE;
      border-radius: 8px;
      padding: 15px 26px;
      display: inline-block;
      font-weight: 600;
    }
  }

  &__content {
    background: #92B621;
    padding: 6px 0;

    .container {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }

  &__menu {
    &-list {
      display: flex;
      align-items: center;
      gap: 90px;
    }
    &-link{
      color: #fff;
      font-weight: 600;
      font-size: 16px;
    }
  }
  &__right{
    display: flex;
    align-items: center;
    gap: 18px;
  }
  &__ln{
    &-link{
      color: #fff;
      font-weight: 600;
    }
  }
}
