// базовые подключения
@import "vars";
@import "mixins";
@import "fonts";
@import "settings";
@import "reset";

.title {
  font-family: "Lato", sans-serif;
  font-size: 48px;
  color: #171717;
  font-weight: 600;
  text-transform: uppercase;
  position: relative;
  line-height: 115%;

  &::before {
    content: '';
    position: absolute;
    background: #92b621;
    width: 29px;
    height: 3px;
    top: -5px;
    left: 49%;
  }
}

.btn {
  width: 100%;
  max-width: 230px;
  padding: 15px;
  font-family: "Lato", sans-serif;
  background: #92B621;
  color: #fff;
  font-weight: 600;
  font-size: 18px;
  border-radius: 8px;
  text-align: center;
  transition: all .2s linear;

  &:hover {
    background: #73920f;
  }
}

.btn-blue {
  font-family: "Gilroy", sans-serif;
  color: #fff;
  background: #4BBEEE;
  border-radius: 8px;
  padding: 15px 26px;
  display: inline-block;
  text-align: center;
  font-weight: 600;
}

.text {
  p {
    line-height: 130%;
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .black {
    font-weight: 600;
  }

  .color {
    color: #4BBEEE;
    font-weight: 600;
  }
}

.breadcrumbs {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  padding: 18px 0;

  li+li {
    display: flex;
    align-items: center;

    &::before {
      content: "";
      background: url(../img/svg/arrow-af.svg) no-repeat;
      font-size: 14px;
      padding-left: 2px;
      width: 7px;
      height: 7px;
      display: block;
      color: #919191;
      top: 0.5px;
      font-weight: 500;
      margin-right: 9px;
      position: relative;
    }
  }

  &__item {
    color: #919191;
    margin-right: 9px;
    font-size: 14px;
    font-weight: 500;

    &:last-child {
      margin-right: 0;
    }

    &::before {}

    &-link {
      color: #4C4B47 !important;
    }
  }
}

.block {
  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__photo {
    width: 50%;
  }

  &__info {
    width: 50%;
  }

  &__title {
    margin-bottom: 30px;

    &::before {
      left: 0;
    }
  }

  &__list {
    width: 100%;
    max-width: 502px;
    padding-left: 30px;
    margin-top: 40px;

    &-item {
      font-size: 14px;
      line-height: 120%;
      position: relative;
      margin-bottom: 26px;

      &::before {
        content: '';
        position: absolute;
        background: url(../img/svg/rhombus.svg) no-repeat;
        width: 10px;
        height: 10px;
        left: -20px;
        top: 4px;
      }

      &:last-child {
        margin-bottom: 0;
      }

      span {
        font-weight: 600;
      }
    }
  }
}

// system //

.system {
  padding: 130px 0 150px 0;

  &__wrapper {
    display: flex;
    justify-content: space-between;
    position: relative;
  }

  &__logo {
    margin-bottom: 35px;
  }

  &__content {
    width: 100%;
    max-width: 737px;
  }

  &__title {
    margin-bottom: 30px;
  }

  &__text {
    width: 100%;
    max-width: 477px;
    font-size: 16px;
    line-height: 130%;

    &-descr {
      margin-bottom: 30px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__photo {
    width: 100%;
    max-width: 700px;
    position: absolute;
    right: -30px;

    img {
      width: 100%;
      max-width: 100%;

    }
  }

  &__text {
    font-size: 16px;

    &-descr {
      margin-bottom: 20px;

      &:last-child {
        margin-bottom: 0;
      }

      .black {
        font-weight: 600;
      }

      .color {
        color: #4BBEEE;
        font-weight: 600;
      }
    }
  }
}


