    margin-bottom: 43px;
  }

  &__content {
    width: 100%;
    max-width: 720px;
    margin-left: auto;
  }

  &__form {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;

    &-input {
      width: 100%;
      max-width: 235px;
