  }

  &__photo {
    position: absolute;
    left: 0;
  }

  &__bottom {}

  &__contacts {
    width: 100%;
    max-width: 720px;
    display: flex;
    align-items: center;
    gap: 62px;
    margin-left: auto;
  }

  &__contact {
    display: flex;
    align-items: center;

    &-icon {
      margin-right: 12px;
    }

    &-subtitle {
      color: #45494f;
      font-family: "Lato", sans-serif;
      font-size: 18px;
      font-weight: 500;
      margin-bottom: 5px;
    }

    &-link {
      display: block;
      color: #171717;
      font-family: "Lato", sans-serif;
      font-size: 20px;
      font-weight: 400;
      letter-spacing: 2%;
