      font-family: "Lato", sans-serif;
      font-size: 48px;
      font-weight: 600;
      display: block;
      text-transform: uppercase;
    }

    .span {
      display: block;
      margin-right: 20px;
      color: rgb(255, 255, 255);
      font-size: 16px;
    }

    .weight {
      font-weight: 600;
    }

    .row {
      display: flex;
      align-items: center;

    }

    .italic {
