    max-width: 232px;

  }
}

.forms {
  padding: 150px 0;

  &__wrapper {
    display: flex;
    justify-content: space-between;
  }

  &__item {
    width: 100%;
    max-width: 511px;
    text-align: center;
    padding: 50px 46px;
    border-radius: 8px;
    box-shadow: 0px 4px 30.4px 0px rgba(169, 165, 165, 0.25);
    background: #ffffff;
    position: relative;
    top: 35px;
    left: -130px;

    &-title {
      color: #171717;
      font-family: "Lato", sans-serif;
      font-size: 32px;
      font-weight: 600;
      line-height: 115%;
      text-transform: uppercase;
      margin-bottom: 20px;
    }

    &-descr {
      width: 100%;
      max-width: 313px;
      font-size: 16px;
      font-weight: 400;
      line-height: 120%;
      margin: 0 auto 30px auto;
    }

    &-input {
      width: 100%;
      height: 50px;
      padding: 14px 16px;
      box-sizing: border-box;
      border: 1px solid rgb(149, 149, 149);
      border-radius: 8px;
      background: #ffffff;
      margin-bottom: 8px;
    }

    &-textarea {
      width: 100%;
      height: 168px;
      padding: 14px 16px;
      box-sizing: border-box;
      border: 1px solid rgb(149, 149, 149);
      border-radius: 8px;
      background: #ffffff;
      margin-bottom: 8px;
      resize: none;
    }

    &-policy {
      font-size: 12px;
      line-height: 140%;
      margin-bottom: 28px;

      a {
        color: #4bbeee;
        text-decoration: underline;
      }
    }

    &-btn {
      width: 100%;
      max-width: 100%
    }
  }

}

.blogs {
