.footer {
  background: #333333;
  color: #fff;
  padding: 60px 0 30px 0;

  a {
    color: inherit
  }

  &__wrapp {
    display: flex;
    justify-content: space-between;
  }

  &__logo {
    width: 100%;
    max-width: 237px;

    span {
      display: block;
      margin-top: 12px;
      color: #ffffffcf;
      font-size: 12px;
      font-weight: 500;
    }
  }

  &__menu {
    &-list {
      display: flex;
      align-items: center;
      gap: 30px;
      margin: 10px 0 34px 0;
    }

    &-link {
      font-size: 14px;
      font-weight: 500;
    }
  }

  &__items {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 80px;
    a {
      display: block;
    }

    span {
      display: block;
    }
  }

  &__item {
    display: flex;
    align-items: center;
    gap: 12px;

    &-link {
      font-size: 14px;
      font-weight: 500;
      margin-bottom: 5px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &-text {
      font-size: 14px;
      font-weight: 500;
      margin-bottom: 5px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__copy {
    text-align: center;
    font-family: "Gilroy", sans-serif;
    font-size: 14px;
    font-weight: 600;
    line-height: 120%;
  }

}
