.swiper-container {
  display: flex;
  height: 842px;
  background: #fff;
}

.main-slider {
  width: 75%;

  height: 100%;
}

.swiper-thumbs {
  width: 118px;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  padding: 98px 0;
  /* Убирает прокрутку */
}

.swiper-thumbs .swiper-wrapper {
  display: flex;
  flex-direction: column;
}

.swiper-thumbs .swiper-slide {
  margin-bottom: 10px;
  height: 118px !important;
  opacity: 0.6;
  cursor: pointer;
  transition: opacity 0.3s;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all .2s linear;
}

.swiper-thumbs .swiper-slide-thumb-active {
  opacity: 1;
  box-sizing: border-box;
  border-radius: 10px;
  border: 4px solid rgb(146, 182, 33);
  box-shadow: 0px 3.72px 28.27px 0px rgba(169, 165, 165, 0.25);

}




.second-thumbs {
  width: 650px;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  padding: 98px 0;
  transition: all .2s linear;
}

.catalogs__icon {
  opacity: 1 !important;
  border-radius: 8px;

  &--first {
    background: #E5E5E5;
  }

  &--second {
    background: #45494F;
  }

  &--third {
    background: #442F29;
  }

}

.second-thumbs .swiper-slide-thumb-active {

  border: 6px solid #92b621;

}

.second-thumbs .swiper-slide {
  width: 170px !important;
  height: 158px !important;
  margin: 0 auto;

  &:hover {
    border: 6px solid #92b621;
  }
}

.second-thumbs {
  padding: 0;
  margin-left: 10px;
}

.second-slider {
  border-radius: 8px;
  box-shadow: 0px 4px 30.4px 0px rgba(169, 165, 165, 0.25);
}

.swiper {
  position: relative;
}

.swiper-button-prev,
.swiper-button-next {
  border-radius: 8px;
  box-shadow: 0px 4px 30.4px 0px rgba(169, 165, 165, 0.25);
  background: #fff;
  width: 42px;
  height: 42px;
}

.swiper-button-next {
  right: 22%;

  &::after {
    content: '';
    background: url(../img/svg/arrow-next.svg) no-repeat;
    width: 12px;
    height: 16px;
  }
}

.swiper-button-prev {
  left: 70%;
  &::after {
    content: '';
    background: url(../img/svg/arrow-prev.svg) no-repeat;
    width: 12px;
    height: 16px;
  }
}

