@mixin tabs {
  .tabs__nav-btn--active {
    background-color: #92B621 !important;
    color: #fff;
  }

  .tabs__panel {
    display: none;
  }

  .tabs__panel--active {
    display: block;
  }
}

.tabs {
  &__title{
    margin-bottom: 40px;
  }
  &__nav {
    display: flex;
    gap: 12px;
    width: 100%;
    max-width: 100%;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px !important;
    &-item{
      width: 100%;
    }
    &-btn {
      width: 100%;
      max-width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #171717;
      font-family: "Lato", sans-serif;
      font-size: 16px;
      font-weight: 600;
      text-transform: uppercase;
      border-radius: 8px;
      background-color: #f4f3f3 !important;
      padding: 25px;
      min-height: 50px;
    }
  }

}

